<template>
    <div v-loading="loading">
        <el-form ref="form" :model="form" label-width="110px">
            <el-row class="toolbar bgc-fff">
                <el-col :span="12">
                    <h2 class="fs-18">当前订单状态：<span  class="color-red">{{form.pur_info.work_status_name}}({{form.pur_info.stage_name}})</span></h2>
                    <p class="fs-14">当前处理人：{{form.pur_info.user_name}}</p>
                </el-col>
                <!-- <el-col :span="12" class="ta-r">
                    <el-button type="primary" icon="plus">导出</el-button>
                    <el-button type="primary" icon="plus">打印</el-button>
                </el-col> -->
            </el-row>
            <el-collapse v-model="collapseActive" @change="">
                <el-collapse-item title="　采购信息" name="1">

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="新车采购单编号">
                                {{form.pur_info.purchase_node}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="销售单编号">
                                {{form.pur_info.sale_node}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-form-item label="供应商">
                        <el-select v-model="form.pur_info.supplier" placeholder="请选择" :disabled="isDisabledBase()">
                            <el-option
                                v-for="item in optionsSupplier"
                                :key="item.id"
                                :label="item.supplier_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    
                </el-collapse-item>

                <el-collapse-item title="　采购信息" name="2">
                    <div class="toolbar ta-r mb10" v-if="isShowAddBtn()">
                        <el-button type="primary" size="mini" plain @click="handleSelectCarType">添加商品</el-button>
                    </div>
                    <el-table :data="form.car_list.rows" border class="table" ref="multipleTable">
                        <el-table-column label="商品信息" width="150">
                            <template slot-scope="{row, $index}">
                                <p>{{row.car_series}} {{row.car_brand}}</p>
                                <p>{{row.horse_power}} {{row.drive_form}} {{row.car_class}}</p>
                                <p>{{row.fuel_type}}</p>
                            </template>
                        </el-table-column>
                        <el-table-column prop="color" label="颜色" width="120">
                        </el-table-column>
                        <el-table-column prop="vin_number" label="vin *17位vin号" width="120">
                        </el-table-column>
                        <template v-if="isShowTable11()">
                            <el-table-column label="采购单价">
                                <template slot-scope="{row}">
                                    <el-input v-model="row.purchase_price"></el-input>
                                </template>
                            </el-table-column>
                            <el-table-column label="数量" width="150">
                                <template slot-scope="{row}">
                                    <el-input-number size="mini" :min="1" :max="100" v-model="row.num" :disabled="form.pur_info.type==1"></el-input-number>
                                </template>
                            </el-table-column>
                            <el-table-column label="小计">
                                <template slot-scope="{row, $index}">
                                    {{jijia(row)}}
                                </template>
                            </el-table-column>
                        </template>
                        <template v-else>
                            <el-table-column prop="purchase_price" label="采购单价"></el-table-column>
                            <el-table-column prop="num" label="数量"></el-table-column>
                            <el-table-column prop="total_price" label="小计"></el-table-column>
                        </template>
                        
                        <el-table-column label="操作" width="180" align="center" fixed="right">
                            <template slot-scope="{row, $index}">
                                <el-button type="text" icon="el-icon-view" @click="handleComplete(row, $index)" v-if="isShowPerfect()">完善信息</el-button>
                                <el-button type="text" icon="el-icon-delete" class="red" @click="handleDelete(row, $index)" v-if="isShowTable11()">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <!-- <div class="pagination">
                        <el-pagination background @current-change="" layout="prev, pager, next" :total="1000">
                        </el-pagination>
                    </div> -->
                    <div class="toolbar ta-r mt10">
                        <span>共选择<b class="color-red fs-16"> {{goodsTotal}} </b>件商品</span>
                        <el-tag type="danger">￥：{{grossAmount}}</el-tag>
                    </div>
                </el-collapse-item>

                <el-collapse-item title="　付款信息" name="3">

                    <el-collapse v-model="collapseGatheringActive">

                        <el-collapse-item title="　定金支付审核" name="1">
    
                            <el-form-item label="费用金额">
                                <el-input v-model="form.pay_info_ding.cost_amount" :disabled="isDisabledPayment1()"></el-input>
                            </el-form-item>

                            <el-form-item label="收款银行">
                                <el-input v-model="form.pay_info_ding.cash_bank" :disabled="isDisabledPayment1()"></el-input>
                            </el-form-item>

                            <el-form-item label="银行开户名">
                                <el-input v-model="form.pay_info_ding.account_name" :disabled="isDisabledPayment1()"></el-input>
                            </el-form-item>

                            <el-form-item label="银行帐号">
                                <el-input v-model="form.pay_info_ding.bank_number" :disabled="isDisabledPayment1()"></el-input>
                            </el-form-item>

                            <el-form-item label="备注">
                                <el-input type="textarea" v-model="form.pay_info_ding.sale_remarkes" :disabled="isDisabledPayment1()"></el-input>
                            </el-form-item>

                            <template v-if="isShowVoucher1()">
                                <el-form-item label="费用凭证">
                                    <el-upload
                                        :disabled="isDisabledPaymentCashier1()"
                                        class="avatar-uploader"
                                        :action="IMG_UPLOAD_URL"
                                        :show-file-list="false"
                                        :on-success="handleUploadSuccess_dj_cost_voucher_img"
                                        :before-upload="handleBeforeUpload">
                                        <img v-if="form.pay_info_ding.cost_voucher" :src="form.pay_info_ding.cost_voucher" class="avatar">
                                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                    </el-upload>
                                </el-form-item>

                                <el-form-item label="备注">
                                    <el-input type="textarea" v-model="form.pay_info_ding.cost_voucher_remarkes" :disabled="isDisabledPaymentCashier1()"></el-input>
                                </el-form-item>
                            </template>

                            

                            <el-form-item label="选择审核人" v-if="isShowFirstAudit()">
                                <el-select v-model="form.pur_info.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                                    <el-option
                                        v-for="item in optionsCompany"
                                        :key="item.id"
                                        :label="item.mc_name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                                <el-select v-model="form.pur_info.first_exec_id" placeholder="请选择审核人">
                                    <el-option
                                        v-for="item in optionsExecUser"
                                        :key="item.id"
                                        :label="item.user_name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>

                        </el-collapse-item>

                        <el-collapse-item title="　尾款支付审核" name="2" v-if="this.form.pur_info.stage_id!=1">
                            
                            <el-form-item label="费用金额">
                                <el-input v-model="form.pay_info_last.cost_amount" :disabled="isDisabledPayment2()"></el-input>
                            </el-form-item>

                            <el-form-item label="收款银行">
                                <el-input v-model="form.pay_info_last.cash_bank" :disabled="isDisabledPayment2()"></el-input>
                            </el-form-item>

                            <el-form-item label="银行开户名">
                                <el-input v-model="form.pay_info_last.account_name" :disabled="isDisabledPayment2()"></el-input>
                            </el-form-item>

                            <el-form-item label="银行帐号">
                                <el-input v-model="form.pay_info_last.bank_number" :disabled="isDisabledPayment2()"></el-input>
                            </el-form-item>

                            <el-form-item label="备注">
                                <el-input type="textarea" v-model="form.pay_info_last.sale_remarkes" :disabled="isDisabledPayment2()"></el-input>
                            </el-form-item>

                            <template v-if="isShowVoucher2()">
                                <el-form-item label="费用凭证">
                                    <el-upload
                                        :disabled="isDisabledPaymentCashier2()"
                                        class="avatar-uploader"
                                        :action="IMG_UPLOAD_URL"
                                        :show-file-list="false"
                                        :on-success="handleUploadSuccess_wk_cost_voucher_img"
                                        :before-upload="handleBeforeUpload">
                                        <img v-if="form.pay_info_last.cost_voucher" :src="form.pay_info_last.cost_voucher" class="avatar">
                                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                    </el-upload>
                                </el-form-item>

                                <el-form-item label="备注">
                                    <el-input type="textarea" v-model="form.pay_info_last.cost_voucher_remarkes" :disabled="isDisabledPaymentCashier2()"></el-input>
                                </el-form-item>
                            </template>

                        </el-collapse-item>

                    </el-collapse>

                </el-collapse-item>

                <el-collapse-item title="　审核信息" name="4" v-if="isShowAudit()">
                    
                    <el-collapse v-model="collapseAuditActive">

                        <el-collapse-item title="　定金支付审核" name="1">
                            <el-card class="box-card mb15" v-for="item in form.exam_dj" :key="item.exec_user_id">
                                <div><span class="display-ib ta-r w80">订单流程：</span>{{item.node_name}}</div>
                                <div><span class="display-ib ta-r w80">审核意见：</span>{{item.exec_op_type}}</div>
                                <div><span class="display-ib ta-r w80">备注：</span>{{item.exec_content}}</div>
                                <div><span class="display-ib ta-r w80">审核人ID：</span> {{item.exec_user_id}}</div>
                                <div><span class="display-ib ta-r w80">审核时间：</span>{{item.create_time | time}}</div>
                            </el-card>
                        </el-collapse-item>

                        <el-collapse-item title="　尾款支付审核" name="2" v-if="this.form.pur_info.stage_id!=1">
                            <el-card class="box-card mb15">
                                <div><span class="display-ib ta-r w80">订单流程：</span>定金审核 - 财务</div>
                                <div><span class="display-ib ta-r w80">审核意见：</span>通过</div>
                                <div><span class="display-ib ta-r w80">备注：</span>撒打算</div>
                                <div><span class="display-ib ta-r w80">审核人ID：</span> asdas</div>
                                <div><span class="display-ib ta-r w80">审核时间：</span>2018-10-23 00:49:38</div>
                            </el-card>
                        </el-collapse-item>

                        <template v-if="isShowOpinion()">
                            <el-form-item label="审核意见" class="mt15">
                                <el-select v-model="form.opinion" placeholder="请选择" class="mr10">
                                    <el-option label="通过" value="1"></el-option>
                                    <el-option label="驳回" value="2"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="备注">
                                <el-input type="textarea" v-model="form.remark"></el-input>
                            </el-form-item>
                            <el-form-item label="选择审核人" v-if="form.pur_info.node_id!=23 && form.pur_info.node_id!=28">
                                <el-select v-model="form.pur_info.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                                    <el-option
                                        v-for="item in optionsCompany"
                                        :key="item.id"
                                        :label="item.mc_name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                                <el-select v-model="form.pur_info.after_user_id" placeholder="请选择审核人">
                                    <el-option
                                        v-for="item in optionsExecUser"
                                        :key="item.id"
                                        :label="item.user_name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </template>

                        <el-form-item label="选择审核人" class="mt15" v-if="isShowLastAudit()">
                            <el-select v-model="form.pur_info.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                                <el-option
                                    v-for="item in optionsCompany"
                                    :key="item.id"
                                    :label="item.mc_name"
                                    :value="item.id">
                                </el-option>
                            </el-select>
                            <el-select v-model="form.pur_info.first_exec_id" placeholder="请选择审核人">
                                <el-option
                                    v-for="item in optionsExecUser"
                                    :key="item.id"
                                    :label="item.user_name"
                                    :value="item.id">
                                </el-option>
                            </el-select>
                        </el-form-item>

                    </el-collapse>
                </el-collapse-item>
            </el-collapse>
            <el-form-item class="form-submit">
                <el-button type="primary" v-loading="loading" @click="handleSubmit(1)" v-if="pageType != 'view'">提交</el-button>
                <el-button type="primary" v-loading="loading" @click="handleSubmit(2)" v-if="isShowSave()">保存</el-button>
                <el-button @click="$router.back()">返回</el-button>
            </el-form-item>
        </el-form>
    

        <!-- 选择车型 -->
        <el-dialog title="选择车型" :visible.sync="dialogSelectCarType" width="50%">
            <selectCar v-if="hackReset" :selectCarOptions="selectCarOptions" @emitCarSubmit="emitCarSubmit" @emitCarClose="emitCarClose"></selectCar>
        </el-dialog>

        <!-- 完善信息 -->
        <el-dialog title="完善信息" :visible.sync="dialogComplete" width="50%">
            <el-form ref="formPerfect" :model="formPerfect" label-width="100px">
                <el-form-item label="车辆商品">
                    <template slot-scope="{row, $index}">
                        <!-- <p>{{row.car_series}} {{row.car_brand}}</p>
                        <p>{{row.horse_power}} {{row.drive_form}} {{row.car_class}}</p>
                        <p>{{row.fuel_type}}</p> -->
                    </template>
                </el-form-item>
                <el-card class="box-card mb10" v-for="(item,index) in arrComplete" :key="index">
                    <el-form-item label="VIN">
                        <el-input v-model="item.vin" placeholder="请输入VIN" v-popover:popover></el-input>
                    </el-form-item>
                    <el-form-item label="购车发票号">
                        <el-input v-model="item.order_id" placeholder="请输入购车发票号"></el-input>
                        <el-upload
                            class="avatar-uploader mt10"
                            :action="IMG_UPLOAD_URL"
                            :show-file-list="false"
                            :on-success="(res,file)=>{return handleUploadSuccess_order_img_url(res,file,index)}"
                            :before-upload="handleBeforeUpload">
                            <img v-if="item.order_img_url" :src="item.order_img_url" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="合格证号">
                        <el-input v-model="item.certificate_id" placeholder="请输入合格证号"></el-input>
                        <el-upload
                            class="avatar-uploader mt10"
                            :action="IMG_UPLOAD_URL"
                            :show-file-list="false"
                            :on-success="(res,file)=>{return handleUploadSuccess_certificate_img_url(res,file,index)}"
                            :before-upload="handleBeforeUpload">
                            <img v-if="item.certificate_img_url" :src="item.certificate_img_url" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>
                </el-card>
                
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogComplete = false">取 消</el-button>
                <el-button type="primary" @click="dialogSubmitComplete">确 定</el-button>
            </span>
        </el-dialog>
        
    </div>
</template>

<script>
    import * as API from '@/api/salesManagement'
    import { organizationListUl } from '@/api/enterpriseManagement'
    import { apiNewCarList } from '@/api/resourceManagement'
    import selectCar from '@/components/selectCar'
    import selectClient from '@/components/selectClient'
    import { requestUrl } from '@/api/config'
    export default {
        data: function(){
            return {
                IMG_UPLOAD_URL: `${requestUrl}/home/UpLoadFile/uploadImg`,
                collapseActive: ['1'],
                collapseAuditActive: ['1'],
                collapseGatheringActive: ['1'],
                dataDelivery: [{
                    name: ''
                }],
                pageType: '',
                addType: '', // 1自动，2手动
                radioActive: '',
                carListActive: '',
                loading: true,
                arrComplete: [],
                itemComplete: {},
                dialogDelivery: false,
                dialogComplete: false,
                couponSum: 12,
                visiblePayment: false,
                visibleEarnest: false,
                hackReset: false,
                dialogSelectCarType: false,
                selectCarOptions:{
                    tableConfig:{
                        car_info: true,
                        yanse: true,
                        gonggao: true,
                        danjia: true,
                    },
                    formConfig: {
                        val: false
                    },
                    pageType: 'salesManagement',
                    isCheckbox: true
                    
                },
                formPerfect: {
                    sale_pur_info_id: '',
                    order_id: '',
                    order_img_url: '',
                    certificate_id: '',
                    certificate_img_url: '',
                    invoice_img: '',
                    invoice_id: '',
                    arr:[
                        {
                            vin:'1',
                            order_id:'1',
                            order_img_url:'',
                            certificate_id:'1',
                            certificate_img_url:'',
                        },
                        {
                            vin:'2',
                            order_id:'2',
                            order_img_url:'',
                            certificate_id:'2',
                            certificate_img_url:'',
                        }
                    ]
                },
                form: 
{
    company: '', // 公司
    opinion: '',
    auditor: '',
    remark: '',
    pur_info: {
        cp_id: "",
        purchase_node: "",
        sale_node: "",
        supplier: "",
        first_exec_id: "",
        after_user_id: "",
        work_id: "",
        work_status: "1",
        work_status_name: "待提交",
        stage_id: "1",
        stage_name: "定金阶段"
    },
    car_list: {
        rows: [],
        total: "1",
        total_num: 3,
        total_price: 639
    },
    pay_info_ding:{
        cost_amount: "",
        cash_bank: "",
        bank_number: "",
        sale_remarkes: "",
        account_name: "",
        pay_type: "2",
        cost_voucher: "",
        cost_voucher_remarkes: "",
        operater_id: "",
        create_time: ""
    },
    pay_info_last:{
        cost_amount: "",
        cash_bank: "",
        bank_number: "",
        sale_remarkes: "",
        account_name: "",
        pay_type: "3",
        cost_voucher: "",
        cost_voucher_remarkes: "",
        operater_id: "",
        create_time: ""
    }
}
                ,
                optionsSupplier:[],
                optionsExecUser:[],
                optionsCompany:[]
            }
        },
        components:{
            selectCar,
            selectClient
        },
        computed:{
            grossAmount(){
                let zong = 0;
                this.form.car_list.rows.forEach((item, index)=>{
                    zong += Number(item.purchase_price)*item.num
                })
                return zong;
            },
            jijia(row){
                return (row) => {
                    return row.num*row.purchase_price
                }
            },
            goodsTotal(){
                let zong = 0;
                this.form.car_list.rows.forEach((item, index)=>{
                    zong += 1*item.num
                })
                return zong;
            }
        },
        mounted(){
            this.getFormData()
        },
        methods: {
            isDisabledBase(){
                if(this.pageType=='view'){
                    return true
                }
                return !(this.form.pur_info.stage_id==1 && this.form.pur_info.work_status==1)
            },
            isDisabledPayment1(){
                if(this.form.pur_info.stage_id == 1 && this.pageType!='view'){
                    return this.form.pur_info.work_status==2 || this.form.pur_info.work_status==4
                }else{
                    return true
                }
            },
            isDisabledPayment2(){
                if(this.form.pur_info.stage_id == 6 && this.pageType!='view'){
                    return this.form.pur_info.work_status==2 || this.form.pur_info.work_status==4
                }else{
                    return true
                }
            },
            isDisabledPaymentCashier1(){
                if(this.form.pur_info.stage_id == 1 && this.pageType!='view'){
                    return this.form.pur_info.work_status!=2 && this.form.pur_info.node_id==23
                }else{
                    return true
                }
            },
            isDisabledPaymentCashier2(){
                if(this.form.pur_info.stage_id == 6 && this.pageType!='view'){
                    return this.form.pur_info.work_status!=2 && this.form.pur_info.node_id==28
                }else{
                    return true
                }
            },
            isShowTable11(){
                if(this.pageType=='view'){
                    return false
                }
                return this.form.pur_info.stage_id==1 && this.form.pur_info.work_status==1
            },
            isShowAddBtn(){
                if(this.pageType=='view'){
                    return false
                }
                return this.form.pur_info.stage_id==1 && this.form.pur_info.work_status==1
            },
            isShowAudit(){
                if(this.form.pur_info.stage_id==1){
                    return this.form.pur_info.work_status!=1
                }else{
                    return true
                }
                
            },
            isShowOpinion(){
                return this.form.pur_info.work_status==2&&this.form.pur_info.node_id!=37
            },
            isShowFirstAudit(){
                return this.form.pur_info.stage_id==1 && this.form.pur_info.work_status==1
            },
            isShowLastAudit(){
                return this.form.pur_info.work_status==1 || this.form.pur_info.work_status==3
            },
            isShowVoucher1(){
                if(this.form.pur_info.stage_id==1){
                    if(this.form.pur_info.node_id>=23 && this.form.pur_info.work_status==2){
                        return true
                    }
                    return false
                }else{
                    return true
                }
            },
            isShowVoucher2(){
                if(this.form.pur_info.stage_id==6){
                    if(this.form.pur_info.node_id>=28 && this.form.pur_info.work_status==2){
                        return true
                    }
                    return false
                }else{
                    return true
                }
            },
            isShowSave(){
                if(this.pageType != 'view' && this.loading==false){
                    return this.form.pur_info.work_status==1 || this.form.pur_info.work_status==3
                }
            },
            isShowPerfect(){
                if(this.form.pur_info.stage_id==1){
                    return this.form.pur_info.work_status!=1
                }else{
                    return this.form.pur_info.work_status!=4
                }
            },
            getFormData() {
                let _path = this.$route.path,
                    _query = this.$route.query;
                _path.indexOf('view') > -1 ? this.pageType='view' : 'edit'
                this.addType = _query.addType

                // 供应商
                apiNewCarList({}).then(res => {
                    if(res.code == 0){
                        this.optionsSupplier = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })

                // 公司列表
                organizationListUl({}).then(res => {
                    if(res.code == 0){
                        this.optionsCompany = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })

                

                // 详情
                if(_query.id){
                    if(this.form.pur_info.stage_id==1){
                        API.newCarManagementView({pur_info_id:_query.id}).then(res => {
                            if(res.code == 0){
                                this.loading = false
                                Object.assign(this.form, res.data);
                                this.getExecUserNodeId()
                            }else{
                                this.$message.error(res.msg)
                            }
                        })
                    }else if(this.form.pur_info.stage_id==2){
                        API.salesManagementBuyCarInfo({pur_info_id:_query.id}).then(res => {
                            if(res.code == 0){
                                this.loading = false
                                Object.assign(this.form, res.data);
                                this.getExecUserNodeId()
                            }else{
                                this.$message.error(res.msg)
                            }
                        })
                    }
                }else{
                    this.loading = false
                    // 初始化号
                    API.newCarManagementGetPurInfo({}).then(res => {
                        this.form.pur_info.purchase_node=res.data.purchase_node
                    })
                    this.getExecUserNodeId()
                }
            },
            getExecUserNodeId(){
                let id = this.form.pur_info.stage_id
                if(id==1){
                    this.execUserNodeId = 33
                }else{
                    this.execUserNodeId = 34
                }
                this.getExecUser();
                
            },
            getExecUser(){
                // 审核人
                let params = {
                    cp_id:this.form.pur_info.cp_id,
                    work_id:this.form.pur_info.work_id,
                    node_id:this.form.pur_info.work_status==1?this.execUserNodeId:this.form.pur_info.node_id,
                }
                API.salesManagementGetExecUser(params).then(res => {
                    if(res.code == 0){
                        this.optionsExecUser = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            handleSubmit(submitType) {

                if(this.form.pur_info.stage_id==1){
                    if(this.form.pur_info.work_status==1 || this.form.pur_info.work_status==3){
                        let params = {
                            base:{
                                submit_type: submitType,
                                type: this.addType,
                                ...this.form.pur_info,
                            },
                            pay_info: {...this.form.pay_info_ding},
                            car_list: this.form.car_list.rows
                        }
                        API.newCarManagementAddPurInfo(params).then(res => {
                            if(res.code == 0){
                                this.$message.success('提交成功！')
                                this.$router.back()
                            }else{
                                this.$message.error(res.msg)
                            }
                            
                        })
                    }else if(this.form.pur_info.work_status==2 && this.form.pur_info.node_id==23){
                       
                        let params = {
                                submit_type: submitType,
                                pur_info_id: this.form.pur_info.pur_info_id, //  采购订单ID 
                                after_user_id: this.form.pur_info.after_user_id, //  审核人ID
                                pay_type: this.form.pay_info_ding.pay_type,
                                cost_voucher: this.form.pay_info_ding.cost_voucher, // 费用凭证图片
                                cost_voucher_remarkes: this.form.pay_info_ding.cost_voucher_remarkes // 凭证备注
                            }
                        
                        API.newCarManagementUpdatePurCost(params).then(res => {
                            if(res.code == 0){
                                this.$message.success('提交成功！')
                                this.$router.back()
                            }else{
                                this.$message.error(res.msg)
                            }
                        })
                    }
                }else if(this.form.pur_info.stage_id==6){
                    if(this.form.pur_info.work_status==1 || this.form.pur_info.work_status==3){
                        let params = {
                            submit_type: submitType,
                            pur_info_id: this.form.pur_info.pur_info_id, //  采购订单ID 
                            cost_amount: this.form.pay_info_last.cost_amount, // 费用金额  
                            cash_bank: this.form.pay_info_last.cash_bank, // 收款银行   
                            account_name: this.form.pay_info_last.account_name, // 收款银行   
                            bank_number: this.form.pay_info_last.bank_number, //   银行账号
                            sale_remarkes: this.form.pay_info_last.sale_remarkes, //  备注
                            first_exec_id: this.form.pur_info.first_exec_id,
                        }
                        API.newCarManagementAddLastPayInfo(params).then(res => {
                            if(res.code == 0){
                                this.$message.success('提交成功！')
                                this.$router.back()
                            }else{
                                this.$message.error(res.msg)
                            }
                        })
                    }else if(this.form.pur_info.work_status==2 && this.form.pur_info.node_id==28){
                        let params = {
                            submit_type: submitType,
                            pur_info_id: this.form.pur_info.pur_info_id, //  采购订单ID 
                            after_user_id: this.form.pur_info.after_user_id, //  审核人ID
                            pay_type: this.form.pay_info_last.pay_type,
                            cost_voucher: this.form.pay_info_last.cost_voucher, // 费用凭证图片
                            cost_voucher_remarkes: this.form.pay_info_last.cost_voucher_remarkes // 凭证备注
                        }
                        API.newCarManagementUpdatePurCost(params).then(res => {
                            if(res.code == 0){
                                this.$message.success('提交成功！')
                                this.$router.back()
                            }else{
                                this.$message.error(res.msg)
                            }
                        })
                    }
                }else if(this.form.pur_info.stage_id==14){
                    // 完善信息
                    let params = {
                        submit_type: submitType,
                        pur_info_id: this.form.pur_info.pur_info_id
                    }
                    API.newCarManagementPerfectLastPay(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }

                // 审核提交
                if(this.form.pur_info.work_status==2 && this.form.pur_info.node_id!=23 && this.form.pur_info.node_id!=28&&this.form.pur_info.stage_id!=14){
                    let auditType,
                        stageId=this.form.pur_info.stage_id
                    if(stageId==1){
                        auditType = 'pur'
                    }else if(stageId==6 || stageId==14){
                        auditType = 'pur_last'
                    }
                    let params = {
                        type: auditType,
                        id:this.form.pur_info.pur_info_id, //  订单id
                        exec_op_type: this.form.opinion, // 审核结果 1 同意 2 驳回
                        exam_remarks:this.form.remark, // 审核备注
                        after_user_id:this.form.pur_info.after_user_id, //   审核人
                        image_url:'',
                        submit_type: submitType
                    }
                    API.salesManagementSubmitSale(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                        
                    })
                }
            },
            handleSelectAuditor(val){
                this.getExecUser();
            },
            handleDelete(row, index){
                this.form.car_list.rows.splice(index, 1);
            },
            handleDelivery(row, index){
                this.dialogDelivery = true
            },
            handleComplete(row, index){
                this.dialogComplete = true
                this.carListActive = index
                Object.assign(this.itemComplete, row)
                this.arrComplete=[]
                if(row.car_info.length > 0){
                    this.arrComplete = row.car_info
                }else{
                    for(let i=0;i<row.num;i++){
                        this.arrComplete.push({
                            vin:'1',
                            order_id:'1',
                            order_img_url:'',
                            certificate_id:'1',
                            certificate_img_url:'',
                        })
                    }
                }
                // Object.assign(this.formPerfect, row)
            },
            dialogSubmitComplete() {
                this.dialogComplete = false;
                // Object.assign(this.form.car_list.rows[this.carListActive], this.formPerfect)
                let params = {
                    pur_record_id: this.itemComplete.pur_record_id,
                    car_list: this.arrComplete
                }
                API.newCarManagementOnePerFectLastPay(params).then(res => {
                    if(res.code == 0){
                        this.dialogComplete = false;
                        this.$message.success('提交成功！')
                    }else{
                        this.$message.error(res.msg)
                    }
                    
                })
            },
            handleSelectDelivery(row, index){

            },
            handleUploadSuccess_dj_cost_voucher_img(res, file, fileList){
                this.form.pay_info_ding.cost_voucher = res.data.url
            },
            handleUploadSuccess_wk_cost_voucher_img(res, file, fileList){
                this.form.pay_info_last.cost_voucher = res.data.url
            },
            handleUploadSuccess_order_img_url(res,file,index){
                this.arrComplete[index].order_img_url = res.data.url
            },
            handleUploadSuccess_certificate_img_url(res,file,index){
                this.arrComplete[index].certificate_img_url = res.data.url
            },
            handleBeforeUpload(){

            },
            handleSelectCarType() {
                this.dialogSelectCarType = true
                this.hackReset = false
                this.$nextTick(() => {
                    this.hackReset = true
                })
            },
            emitCarClose(){
                this.dialogSelectCarType = false;
            },
            emitCarSubmit(row){
                this.dialogSelectCarType = false;
                let arr = this.form.car_list.rows.concat(row)
                this.form.car_list.rows = arr
            }
        }
    }
</script>

